import React from 'react'
import { useState } from 'react'
import s from './Pagination.module.scss'

const Pagination = (
    {totalUsersCount, pageCount, currentPage, setCurrentPage, portionPage = 15}
    ) => {
    let paginationItemsCount = Math.ceil(totalUsersCount / pageCount)
    let portionItemsCount = Math.ceil(totalUsersCount / portionPage)
    let [portionCurrentItem, setportionCurrentItem] = useState( ()=>1 )
    let leftPortionPageNumber = (portionCurrentItem - 1) * portionPage + 1
    let rightPortionPageNumber = portionCurrentItem * portionPage
    let paginationItemsList = []
    for(let i = 1; i<= paginationItemsCount; i++){
        paginationItemsList.push(i)
    }
    let paginationItems = paginationItemsList.map(el => {
        return (
            <span className={currentPage === el ? s.PaginationItemActive : s.PaginationItem} onClick={() => {setCurrentPage(el)}}>{el}</span>
        )
    })
    return (
        <div className={s.Pagination}>
            {paginationItems}
        </div>
    )
}

export default Pagination