import React from 'react';
import s from './User.module.css';
import { NavLink } from 'react-router-dom';
import userPhoto from '../../../assets/images/default-avatar.jpg'
const User = (props) => {
    return (
        <div className={s.User}>
            <div className={s.User__body}>
                <div className={s.User__avatar}>
                    <div className={s.User__avatar_img}>
                        <NavLink to={'/profile/' + props.id}>
                            <img src={userPhoto} alt="#" />
                        </NavLink>
                    </div>
                    <div className={s.User__avatar_btn} >
                        {props.followed ? <button onClick={ () => {props.deleteFollowStatus(props.id)} }>Удалить</button> : <button  onClick={ () => {props.addFollowStatus(props.id)} }>Добавить</button>}
                    </div>
                </div>
                <div className={s.User__content}>
                    <div className={s.User__content_top}>
                        <div className={s.User__content_name}>{props.name}</div>
                        <div className={s.User__content_location}>{props.location}</div>
                    </div>
                    <div className={s.User__content_footer}>
                        <div className={s.User__content_message}>HI</div>
                    </div>
                </div>
            </div>

        </div>
    )
}
export default User
