import React from 'react';
import Pagination from '../Pagination/Pagination';
import User from './User/User';
import s from './Users.module.css';


const Users = (props) => {

    let usersList = props.users.map((el) => {
        return (
            <User key={el.id} followingInProgress={props.followingInProgress} isAuth={props.isAuth} id={el.id} deleteFollowStatus={props.deleteFollowStatus} addFollowStatus={props.addFollowStatus} name={el.name} avatar={el.photos.small} followed={el.followed} />
        )
    })
    return (
        <div className={s.Users}>
            <div className={s.Users__pagination}>
                <Pagination
                    totalUsersCount={props.totalUserCount}
                    pageCount={props.count}
                    currentPage={props.page}
                    setCurrentPage={props.setCurrentPage}
                />
            </div>
            {usersList}
        </div>
    )
}
export default Users

