import React from 'react';
import Users from './Users';
import { connect } from "react-redux";
import preloading from '../../assets/images/preloading.gif'
import {getCurrentPage, setTotalUsersCount, toggleIsFetching, getUsersThunkCreator, postUsersThunkCreator, deleteUsersThunkCreator } from "../../redux/users-reducer";

class UsersContainer extends React.Component {
    componentDidMount = () => {
        this.props.getUsersThunkCreator(this.props.count, this.props.page)
    }
    render() {
        const deleteFollowStatus = (id) => {
            this.props.deleteUsersThunkCreator(id)
        }
        const addFollowStatus = (id) => {
            this.props.postUsersThunkCreator(id) 
        }
        const setCurrentPage = (el) => {
            this.props.getCurrentPage(el)
            this.props.getUsersThunkCreator(this.props.count, this.props.page)
        }
        return (
            <>
                {this.props.isFetching ? <img src={preloading} alt="Loading" /> : null}
                <Users {...this.props} deleteFollowStatus={deleteFollowStatus} addFollowStatus={addFollowStatus} setCurrentPage={setCurrentPage} />
            </>
        )
    }
}



const mapStateToProps = (state) => {
    return {
        users: state.users.users,
        count: state.users.count,
        totalUserCount: state.users.totalUsersCount,
        page: state.users.page,
        isFetching: state.users.isFetching,
        followingInProgress: state.users.followingInProgress
    }
}



export default connect(mapStateToProps,
    {
        getCurrentPage,
        setTotalUsersCount,
        toggleIsFetching,
        getUsersThunkCreator,
        postUsersThunkCreator,
        deleteUsersThunkCreator
    }
)(UsersContainer)